import React, {Component} from 'react';
import './App.css';
import Body from './Body.js'
import SearchTool from './tools/searchtool.js'
import Category from './Category.js';
import Product from './Product.js';
import Cart from './tools/cart.js';

import {BrowserRouter as Router,Switch,Route,Link} from "react-router-dom";
import {HashRouter} from "react-router-dom";


class App extends Component {
	
	constructor(props){
		super(props)
		this.child = React.createRef();
		this.state = {child:false}

	}
	
	
	componentDidMount(){
		this.state.child = this.child
		this.setState({child:this.child})
		

		
	}




	clickCart(){
		if(this.child.state.visible == "block"){
			this.child.setState({visible:"none"})
		}else{
			this.child.setState({visible:"block"})
		}
		
		
	}

	
	

	render(){
		
	
	
  return (
    <div className="App">
      <header className="App-header">
		<span className="logo">Shoopping</span>
		
		
		
		<SearchTool />
		<div className="topMenu">
			<span className="navibar">

				<div className="cat"><a href={process.env.PUBLIC_URL + '/home'}>Home</a></div>
				<div className="cat"><a href={process.env.PUBLIC_URL + '/category/shoes'}>Shoes</a></div>
				<div className="cat"><a href={process.env.PUBLIC_URL + '/category/thirts'}>T-Shirts</a></div>
				<div className="cat"><a href={process.env.PUBLIC_URL + '/category/pants'}>Pants</a></div>
			</span>
			
			
			
			<span className="cart">
				<img src={require("./img/cart3.png")} onClick={() => this.clickCart()}></img>

			</span>
			<Cart ref={cd => (this.child = cd)} />
			
		
		</div>
		
      </header>
	  
	 
	  
	  
		<HashRouter basename={process.env.PUBLIC_URL}>
			<Switch>
			  <Route exact path={process.env.PUBLIC_URL}>
				<Body cartHook={this.child} />
			  </Route>
			  <Route path={process.env.PUBLIC_URL + "/home"}>
				<Body cartHook={this.child} />
			  </Route>
			  <Route path="/category">
				<Category />
			  </Route>
			</Switch>
		</HashRouter>
	  
	  <div className="footer">
		Terms of Service<br />
		FAQ<br />
		Rules<br />
		Contact<br />
	  
	  </div>
	  
	  
	  

	 
		
	  
	  
    </div>
  );
	}
}

export default App;
